@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

* {
	box-sizing: border-box;
}

body {
	background: #f6f5f7;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Montserrat', sans-serif;
	height: 120vh;
	margin: -20px 0 50px;
}

.container {
	background-color: #fff;
	border-radius: 3px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 968px;
	max-width: 100%;
	min-height: 700px;
	top: 30px;

}
.form-group i {
  position: relative;
  left: -2px;
  top: -38px;
  padding: 9px 8px;
  color:#00baeb;
  transition: 0.3s;
}
#feilds{
	margin-bottom: -10px;
}

.form-group{
  position: relative;
  left: 25px;
}
::placeholder {
  
  position: relative;
	left: 20px;
	bottom: 5%;
	font-size: 13px;
	color: #bbbbbb;

	
	
}
.form-group img{
	position: relative;
	right: 10%;
	top: 40px;
	left: 75px;
	width: 50%;
	height: 50%;
	padding-bottom:50px;
}

.btn {
  background-color:#00baeb;
  color: white;
  padding: 8px 20px;
  border: none;
  cursor: pointer;
	width: 88%;
	position: relative;
	right: 0px;
	top: 10px;
  opacity: 0.9;
}

.btn:hover {
  opacity: 1;
}




.link{
	Cursor:text !important; Text-Decoration: None !important; 
	
}

#input_width{
	width: 90%;
  border: #FFFFFF;
  
	
}








