@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
	box-sizing: border-box;
}

body {
	background: #f6f5f7;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Montserrat', sans-serif;
	height: 120vh;
	margin: -20px 0 50px;
}

.container {
	background-color: #fff;
	border-radius: 3px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 968px;
	max-width: 100%;
	min-height: 700px;
	top: 30px;

}
.form-group i {
  position: relative;
  left: -2px;
  top: -38px;
  padding: 9px 8px;
  color:#00baeb;
  transition: 0.3s;
}
#feilds{
	margin-bottom: -10px;
}
#date{
	position: relative;
	left: 130px;
	bottom: 20px;
	font-size: 13px;
	color: #707070;
	text-align: center;

}
#date1{
	position: relative;
	left: 155px;
	bottom: 20px;
	font-size: 13px;
	color: #707070;
	text-align: center;
}
#pp{
	font-size: 10px;
	color: #707070;
	position: relative;
	right: 10px;
	text-align: center;
	
}
::-webkit-input-placeholder {
  
  position: relative;
	left: 20px;
	bottom: 1%;
	font-size: 13px;
	color: #707070;

	
	
}
:-ms-input-placeholder {
  
  position: relative;
	left: 20px;
	bottom: 1%;
	font-size: 13px;
	color: #707070;

	
	
}
::-ms-input-placeholder {
  
  position: relative;
	left: 20px;
	bottom: 1%;
	font-size: 13px;
	color: #707070;

	
	
}
::placeholder {
  
  position: relative;
	left: 20px;
	bottom: 1%;
	font-size: 13px;
	color: #707070;

	
	
}
.form-group img{
	position: relative;
	right: 10%;
	top: 40px;
	left: 75px;
	width: 50%;
	height: 50%;
	padding-bottom:50px;
}

.btn {
  background-color:#00baeb;
  color: white;
  padding: 8px 20px;
  border: none;
  cursor: pointer;
	width: 88%;
	position: relative;
	right: 0px;
	top: 10px;
  opacity: 0.9;
}

.btn:hover {
  opacity: 1;
}


#example-date-input{
	position: relative;
	bottom: 18px;
	width: 90%;
	padding: 10px;
	border: none;
	color: #bbbbbb;
	font-size: 15px;

  
}



#input_width{
	width: 90%;
	border: #FFFFFF;
	
}


.radio{
	position: relative;
	top: 0px;
	font-size: 13px;
	color: #707070;
	left: 155px;

}





#left{
	background-color: rgb(250, 250, 250);
	position: absolute;
	overflow: hidden;
  width: 50%;
	left: 0px;
	top: 0px;
	height: 580px;
	border: none;
}
#right{
	background-color:#00baeb;
	position: absolute;
	overflow: hidden;
  width: 53%;
	left: 470px;
	top: 0px;
	height: 580px;
	border: none;
}

.rightside{
	color: white;
	position: relative;
	top: 10%;
	text-align: center;
}
.rightside h3{
	font-size:15px;
	
}
#rightsidelable{
font-size: 12px;
position: relative;
top: 30px;

}
.rightside img{
	position: relative;
	right: 0%;
	top: 100px;
	width: 70%;
	height: 80%;
	
}





